.page-header {
  z-index: 11;
  min-height: 120px;
  color: $color-default-white;
}

.page-header__scroll {
  background-color: $color-default-black;
  background: url('../img/header-background.png') no-repeat center / cover;

  @include retina {
    background-image: url("../img/header-background@2x.png");
  }

  @media (min-width: $vp-1024) {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
  }

}

.page-header__inner {
  display: block;
  width: 100%;
  max-width: 1222px;
  margin: 0 auto;
  padding: 30px 0;

  @include vp-1023 {
    padding: 20px 26px;
  }
}

.page-header__top {
  position: relative;
  display: flex;
  flex-direction: row;
}

.page-header__logo {
  margin-right: 160px;

  @include vp-1023 {
    margin-right: 20px;
  }
}

.page-header__info {
  @include vp-1023 {
    margin-right: 15px;
  }
}
